@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,700);
.banner,
.banner__logo,
.banner__text {
  height: 72px;
}

.banner__logo {
  background-image: url('data:image/png;base64,');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 0 17px;
  width: 90px;
  font-size: 0;
  float: left;
}

.banner__text {
  color: #90a2bd;
  font-size: 15px;
  float: left;
  margin: 0 30px 0 0;
  line-height: 72px;
}

.banner__img {
  position: absolute;
  top: 0;
  right: 0;
  border: 0;
}

.searchbox__select {
  /* Reset appearance */
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  outline: 0;
  box-shadow: none;
  border: 0;
  background: transparent;
  background-image: none;
  font-size: inherit;
  font-weight: 400;
  border-radius: 0;
  /* Styling */
  cursor: pointer;
  border-bottom: 1px dashed #111;
  color: #000;
  padding: 5px;
}

.searchbox__select:hover {
  background-color: #111;
  color: #fff;
}
.searchBox {
  background-image: linear-gradient(45deg, #0abf53, #4ee5e5);
  padding: 60px 0;
  color: #fff;
  font-size: 30px;
}
.venue {
  background-color: #f4f4f4;
  border-radius: 5px;
  padding: 24px;
  margin-bottom: 24px;
}

.venue__header {
  display: block;
  margin-bottom: 24px;
}

.venue__header h3 {
  font-size: 24px;
  font-weight: 600;
  margin: 0;
}

.venue__address {
  display: inline-block;
}

.venue__categs {
  margin: 0;
  padding: 0;
  list-style: none;
}

.venue__categ {
  font-size: 12px;
  padding: 3px 6px;
  border-radius: 3px;
  color: #fff;
  background-color: #00112c;
  display: inline-block;
  margin-right: 12px;
}

.venue__content {
  display: block;
}

.venue__distance {
  display: inline-block;
}

.venue__distance p {
  border: 1px solid #00112c;
  font-size: 24px;
  font-weight: 300;
}

.venue__maps {
  display: inline-block;
  margin-left: 15px;
}

.venue__maps a {
  display: block;
  background-image: linear-gradient(45deg, #0abf53, #4ee5e5);
  color: #fff;
  font-size: 13px;
}

.venue__distance p,
.venue__maps a {
  border-radius: 5px;
  display: block;
  width: 125px;
  height: 55px;
  line-height: 55px;
  text-align: center;
  margin: 0;
  overflow: hidden;
  letter-spacing: normal;
}

@media (min-width: 1024px) {
  .venue {
    display: flex;
  }
  
  .venue__header {
    display: inline-block;
    margin-bottom: 0;
    width: 100%;
  }
  
  .venue__content {
    display: inline-block;
    width: 280px;
    font-size: 0;
  }
  
  .venue__distance p,
  .venue__maps a {
    width: 105px;
    height: 105px;
    line-height: 105px;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: 'Open Sans', sans-serif;
  margin: 0;
  margin-bottom: 50px;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  background-color: #fff;
  font-weight: 300;
  letter-spacing: .04em;
}

.innerContainer {
  margin: 0 auto;
  max-width: 1248px;
  padding-right: 24px;
  padding-left: 24px;
  width: 100%;
}

.commonTitle {
  font-size: 28px;
  font-weight: 400;
  color: #00112c;
}

@media (min-width: 1024px) {
  .commonTitle {
    font-size: 32px;
  }
}
